.burger__btn{
  display: none;
  z-index: 20;
}

.burger__container{
  position: relative;
  z-index: 20;
}

.burger__icon{
  background-color: #000000;
  display: block;
  height: 3px;
  width: 28px;
  position: relative;
  transition: background .2s ease-out;
  cursor: pointer;
  z-index: 20;
}

.burger__icon:before,
.burger__icon:after{
  background: inherit;
  content: "";
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  transition: all .2s ease-out;
  cursor: pointer;
  top: 7px;
}

.burger__icon:before{
  top: 7px;
}

.burger__icon:after{
  top: -7px;
}

.burger__btn:checked ~ .burger__container .burger__icon{
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  background: none;
}

.burger__btn:checked ~ .burger__container .burger__icon:before{
  -webkit-transform: rotate(0);
          transform: rotate(0);
  top: 0;
}

.burger__btn:checked ~ .burger__container .burger__icon:after{
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  top: 0;
}

.burger__btn:checked ~ .navigation{
  visibility: visible;
  display: flex;
}

@media screen and (min-width: 1280px){
  .burger{
    display: flex;
  }

  .burger__container{
    display: none;
  }
}

.navigation{
  visibility: hidden;
  position: fixed;
  opacity: 1;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity 1s ease-in-out;
  z-index: 10;
}

.navigation__list{
  display: flex;
  position: fixed;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 320px;
