.switch{
  position: relative;
  display: inline-block;
  width: 36px;
  height: 20px;
  margin: 0 13px 0 0;
}

.slider{
  position: absolute;
  cursor: pointer;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #ccc;
  transition: .4s;
  border-radius: 20px;
}

.switch .checkbox{
  display:none
}

.slider:before{
  position: absolute;
  content: "";
  height: 8px;
  width: 8px;
  left: 6px;
  bottom: 6px;
  background-color: #FFFFFF;
  transition: 0.4s;
  border-radius: 20px;
}

.checkbox:checked + .slider{
  background-color: #2BE080;
}

.checkbox:checked + .slider::before{
  -webkit-transform: translateX(15px);
          transform: translateX(15px);
}

